<template>
  <div id="app">
    <Header></Header>
    <router-view/>
    <Footer></Footer>
  </div>
</template>


<script>
  import Header from './components/Header.vue'
  import Footer from './components/Footer.vue'
  import {COMPANY, USER} from "./store/mutation-types";
  import {USER_PROFILE, GET_PROFILE as USER_GET_PROFILE} from "./store/types/userProfile";
  import {COMPANY_PROFILE, GET_PROFILE as COMPANY_GET_PROFILE} from "./store/types/companyProfile";

  export default {
      name: 'app',
      components: {
          Header,
          Footer
      },
      computed: {

          isLogged() {
              //    storage.
              //  false( ),
              //            'USER'(   ),
              //            'COMPANY'(   )
              return this.$store.getters['auth/isLogged']
          },

      },
      beforeMount() {
          if (this.isLogged) { //isLogged -   computed()
              this.$router.push('/') //       
          } else if (this.isLogged === USER) {
              this.$store.dispatch(USER_PROFILE + USER_GET_PROFILE) //          
          } else if (this.isLogged === COMPANY) {
              this.$store.dispatch(COMPANY_PROFILE + COMPANY_GET_PROFILE)  //          
          }
      }
  }
</script>

<style lang="scss">
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100vw;
  overflow: hidden;
}
